.square {
  position: relative;
  padding: 0 10px;
  border-radius: 10px;
  opacity: 0.9;
}

.square h3 {
  z-index: 20;
  width: 100%;
  margin-bottom: 20px;
  text-align: center;
}

.square .list {
  position: relative;
  display: block;
  height: 260px;
  padding: 5px 15px;
  overflow-y: scroll;
}

.square .list::-webkit-scrollbar {
  display: none;
}
